'use client';

import { useState, useEffect } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { Header } from '@/components/Header';
import { EmailReportModal } from '@/components/EmailReportModal';
import { CalculationInputs, CalculationResults, calculateCaseload, formatCurrency, formatRange } from '@/lib/calculations';
import { analytics } from '@/lib/analytics';

export default function ReviewPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [results, setResults] = useState<CalculationResults | null>(null);
  const [inputs, setInputs] = useState<CalculationInputs | null>(null);
  const [isEmailModalOpen, setIsEmailModalOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Parse inputs from URL
    const hours = parseFloat(searchParams.get('hours') || '0');
    const income = parseFloat(searchParams.get('income') || '0');
    const fee = parseFloat(searchParams.get('fee') || '0');
    const cancellations = parseFloat(searchParams.get('cancellations') || '0');

    if (hours > 0 && income > 0 && fee > 0 && cancellations >= 0) {
      const parsedInputs: CalculationInputs = {
        weeklyHours: hours,
        monthlyIncome: income,
        sessionFee: fee,
        noShowRate: cancellations
      };

      setInputs(parsedInputs);
      const calculatedResults = calculateCaseload(parsedInputs);
      setResults(calculatedResults);
      setIsLoading(false);
    } else {
      // Invalid params, redirect to plan
      router.push('/plan');
    }
  }, [searchParams, router]);

  const handleAdjustGoals = () => {
    if (!inputs) return;

    // Navigate back to /plan with current inputs
    const params = new URLSearchParams({
      hours: inputs.weeklyHours.toString(),
      income: inputs.monthlyIncome.toString(),
      fee: inputs.sessionFee.toString(),
      cancellations: inputs.noShowRate.toString()
    });

    router.push(`/plan?${params.toString()}`);
  };

  const handleEmailReport = () => {
    analytics.emailReportOpened();
    setIsEmailModalOpen(true);
  };

  if (isLoading || !results || !inputs) {
    return (
      <div className="min-h-screen bg-nesso-bg flex items-center justify-center">
        <div className="flex flex-col items-center space-y-4">
          <div className="w-12 h-12 border-4 border-nesso-peach/20 border-t-nesso-peach rounded-full animate-spin"></div>
          <p className="text-nesso-ink/60">Loading your plan...</p>
        </div>
      </div>
    );
  }

  const hoursLeft = Math.max(inputs.weeklyHours - results.totalHours, 0);
  const hoursOver = Math.max(results.totalHours - inputs.weeklyHours, 0);

  const getStatusMessage = () => {
    switch (results.sustainabilityStatus) {
      case 'under-goal':
        return `This plan uses about ${results.totalHours.toFixed(1)}h, leaving ${hoursLeft.toFixed(1)}h within your ${inputs.weeklyHours}h goal.`;
      case 'sustainable':
        return `This plan fits within your ${inputs.weeklyHours}h goal. Right on target.`;
      case 'over-goal':
        return `This plan requires ${results.totalHours.toFixed(1)}h, which is ${hoursOver.toFixed(1)}h over your ${inputs.weeklyHours}h goal.`;
    }
  };

  const getStatusText = () => {
    switch (results.sustainabilityStatus) {
      case 'sustainable':
        return 'Balanced';
      case 'over-goal':
        return 'Not sustainable';
      case 'under-goal':
        return 'Room to spare';
    }
  };

  const sessionPercentage = Math.min((results.sessionHours / inputs.weeklyHours) * 100, 100);
  const docPercentage = Math.min((results.docHours / inputs.weeklyHours) * 100, 100 - sessionPercentage);
  const isOverGoal = results.totalHours > inputs.weeklyHours;

  return (
    <div className="min-h-screen bg-nesso-bg flex flex-col">
      <Header />

      <main className="flex-1 container mx-auto max-w-6xl px-4 py-12 space-y-12">
        {/* Header */}
        <section className="text-center space-y-4">
          <h1 className="text-3xl md:text-4xl font-semibold tracking-tight text-nesso-navy mb-2">
            Your caseload plan
          </h1>
        </section>

        {/* Results Card */}
        <section className="max-w-4xl mx-auto">
          <div className="bg-nesso-card rounded-2xl ring-1 ring-black/5 shadow-sm p-8 space-y-8">
            {/* Top Summary - Two Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              <div className="text-center md:text-left">
                <h3 className="text-lg font-medium text-nesso-navy mb-2">Weekly caseload needed</h3>
                <div className="text-3xl font-semibold text-nesso-navy mb-1">
                  {formatRange(results.caseloadRange.low, results.caseloadRange.high, ' clients')}
                </div>
                <p className="text-sm text-nesso-ink/60">
                  To meet your {formatCurrency(inputs.monthlyIncome)}/mo goal in {inputs.weeklyHours}h/wk, you&apos;d need {formatRange(results.caseloadRange.low, results.caseloadRange.high)} clients per week at {formatCurrency(inputs.sessionFee)}.
                </p>
              </div>

              <div className="text-center md:text-left">
                <h3 className="text-lg font-medium text-nesso-navy mb-2">Actual revenue projection</h3>
                <div className="text-3xl font-semibold text-nesso-navy mb-1">
                  {formatCurrency(results.revenueProjection)}
                </div>
                <p className="text-sm text-nesso-ink/60">per month</p>
              </div>
            </div>

            {/* Parameters Summary (read-only) */}
            <div className="border-t border-nesso-navy/10 pt-6">
              <div className="flex items-start justify-between gap-4 mb-4">
                <h3 className="text-base font-medium text-nesso-navy">Your parameters</h3>
                <button
                  onClick={handleAdjustGoals}
                  className="px-4 py-2 text-sm rounded-lg border border-nesso-navy/20 text-nesso-navy hover:bg-nesso-navy/5 transition-colors focus:ring-2 focus:ring-nesso-navy/20"
                  aria-label="Adjust your goals and return to planning page"
                >
                  Adjust goals
                </button>
              </div>

              {/* Parameters Grid - read-only chips */}
              <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                <div className="bg-nesso-navy/5 rounded-lg px-3 py-2">
                  <div className="text-xs text-nesso-ink/60 mb-1">Weekly hours</div>
                  <div className="text-sm font-medium text-nesso-navy">{inputs.weeklyHours}h</div>
                </div>
                <div className="bg-nesso-navy/5 rounded-lg px-3 py-2">
                  <div className="text-xs text-nesso-ink/60 mb-1">Monthly income</div>
                  <div className="text-sm font-medium text-nesso-navy">{formatCurrency(inputs.monthlyIncome)}</div>
                </div>
                <div className="bg-nesso-navy/5 rounded-lg px-3 py-2">
                  <div className="text-xs text-nesso-ink/60 mb-1">Session fee</div>
                  <div className="text-sm font-medium text-nesso-navy">{formatCurrency(inputs.sessionFee)}</div>
                </div>
                <div className="bg-nesso-navy/5 rounded-lg px-3 py-2">
                  <div className="text-xs text-nesso-ink/60 mb-1">Cancellations</div>
                  <div className="text-sm font-medium text-nesso-navy">{inputs.noShowRate}/wk</div>
                </div>
              </div>
            </div>

            {/* Visualization */}
            <div className="border-t border-nesso-navy/10 pt-6 space-y-4">
              <h3 className="text-base font-medium text-nesso-navy mb-4">Time breakdown</h3>

              {/* Workload Bar */}
              <div className="space-y-3">
                <div className="relative w-full">
                  <div className="relative w-full h-8 overflow-hidden">
                    {/* Base track */}
                    <div className="absolute inset-0 bg-nesso-navy/10 rounded-md" />

                    {/* Progress fill */}
                    <div className="relative h-full flex overflow-hidden">
                      {/* Sessions segment */}
                      <div
                        className="bg-nesso-coral flex items-center justify-center text-black text-xs font-medium min-w-0 relative"
                        style={{ width: `${(results.sessionHours / inputs.weeklyHours) * 100}%` }}
                        title={`Sessions: ${results.sessionHours.toFixed(1)}h`}
                      >
                        <span className="hidden md:block text-xs font-medium truncate px-2">
                          {results.sessionHours > 3 && sessionPercentage > 20 ? `Sessions ${results.sessionHours.toFixed(1)}h` : ''}
                        </span>
                      </div>

                      {/* Documentation segment */}
                      <div
                        className="bg-nesso-purple flex items-center justify-center text-white text-xs font-medium min-w-0 relative"
                        style={{ width: `${(results.docHours / inputs.weeklyHours) * 100}%` }}
                        title={`Documentation: ${results.docHours.toFixed(1)}h`}
                      >
                        <span className="hidden md:block text-xs font-medium truncate px-2">
                          {results.docHours > 2 && docPercentage > 15 ? `Documentation ${results.docHours.toFixed(1)}h` : ''}
                        </span>
                      </div>
                    </div>

                    {/* Overflow indicator */}
                    {isOverGoal && (
                      <div
                        className="absolute top-0 bg-red-500 h-full flex items-center justify-center text-white text-xs font-medium"
                        style={{
                          left: '100%',
                          width: `${Math.min(((results.totalHours - inputs.weeklyHours) / inputs.weeklyHours) * 100, 50)}%`,
                        }}
                        title={`Over goal by ${hoursOver.toFixed(1)}h`}
                      >
                        <span className="text-xs font-medium px-2">Over</span>
                      </div>
                    )}
                  </div>

                  {/* Edge labels */}
                  <div className="flex justify-between items-center mt-1">
                    <div className="text-xs text-nesso-ink/60 font-medium">0hrs</div>
                    <div className="text-xs text-nesso-ink/60 font-medium">Goal: {inputs.weeklyHours}h</div>
                  </div>

                  {/* Legend */}
                  <div className="mt-3 flex flex-wrap gap-4 justify-center text-xs">
                    <div className="flex items-center gap-1">
                      <div className="w-3 h-3 bg-nesso-coral rounded-sm"></div>
                      <span>Sessions {results.sessionHours.toFixed(1)}h</span>
                    </div>
                    <div className="flex items-center gap-1">
                      <div className="w-3 h-3 bg-nesso-purple rounded-sm"></div>
                      <span>Documentation {results.docHours.toFixed(1)}h</span>
                    </div>
                    {hoursLeft > 0 && (
                      <div className="flex items-center gap-1">
                        <div className="w-3 h-3 bg-nesso-navy/20 rounded-sm"></div>
                        <span>{hoursLeft.toFixed(1)}h available</span>
                      </div>
                    )}
                    {hoursOver > 0 && (
                      <div className="flex items-center gap-1">
                        <div className="w-3 h-3 bg-red-500 rounded-sm"></div>
                        <span>{hoursOver.toFixed(1)}h over</span>
                      </div>
                    )}
                  </div>
                </div>
              </div>

              {/* Insight sentence */}
              <div
                className="text-sm text-nesso-ink/80"
                aria-live="polite"
                aria-atomic="true"
              >
                <div className="flex items-start gap-3">
                  <p className="flex-1">{getStatusMessage()}</p>
                  <div className={`
                    inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium flex-shrink-0
                    ${results.sustainabilityStatus === 'under-goal' ? 'bg-green-100 text-green-800' : ''}
                    ${results.sustainabilityStatus === 'sustainable' ? 'bg-blue-100 text-blue-800' : ''}
                    ${results.sustainabilityStatus === 'over-goal' ? 'bg-red-100 text-red-800' : ''}
                  `}>
                    {getStatusText()}
                  </div>
                </div>

                {/* Documentation footnote */}
                <div className="flex items-center gap-2 mt-3 text-xs text-nesso-ink/60">
                  <svg
                    className="w-3.5 h-3.5 flex-shrink-0"
                    viewBox="0 0 16 16"
                    fill="currentColor"
                    aria-hidden="true"
                  >
                    <path fillRule="evenodd" d="M15 8A7 7 0 1 1 1 8a7 7 0 0 1 14 0ZM9 5a1 1 0 1 1-2 0 1 1 0 0 1 2 0ZM6.75 8a.75.75 0 0 0 0 1.5h.75v1.75a.75.75 0 0 0 1.5 0v-2.5A.75.75 0 0 0 8.25 8h-1.5Z" clipRule="evenodd" />
                  </svg>
                  <span>Includes 20 minutes per client per week for documentation.</span>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* Bottom CTA */}
        <section className="flex justify-center">
          <button
            onClick={handleEmailReport}
            className="px-8 py-3 text-base rounded-xl bg-nesso-coral hover:bg-nesso-coral/90 text-black font-medium shadow-sm transition-colors focus:ring-2 focus:ring-nesso-coral"
          >
            Email me my caseload plan
          </button>
        </section>
      </main>

      {/* Email Report Modal */}
      {results && inputs && (
        <EmailReportModal
          isOpen={isEmailModalOpen}
          onClose={() => setIsEmailModalOpen(false)}
          results={results}
          inputs={inputs}
        />
      )}
    </div>
  );
}
